@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");

body {
	padding: 0;
	margin: 0;
}

header {
	text-align: center;
	margin: 0 auto;
	font-family: "Inter", sans-serif;
	font-size: 16px;
	width: 80%;
	border: 1px solid red;
	padding: 3%;
}

#appTitle {
	color: #3730a3;
	padding-bottom: 3%;
}

#appIntro {
	color: #818cf8;
}

